
.vc-leaflet-control-minimap {
  border: solid rgba(255, 255, 255, 1) 4px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.65);
  border-radius: 3px;
  background: #f8f8f9;
  transition: all 0.6s;
  box-sizing: unset;
  position: absolute;
  z-index: 10;
}

.vc-leaflet-control-minimap a {
  background-color: rgba(255, 255, 255, 1);
  background-repeat: no-repeat;
  z-index: 99999;
  transition: all 0.6s;
}

.vc-leaflet-control-minimap a.minimized-bottomright {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  border-radius: 0px;
}

.vc-leaflet-control-minimap a.minimized-topleft {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  border-radius: 0px;
}

.vc-leaflet-control-minimap a.minimized-bottomleft {
  -webkit-transform: rotate(270deg) !important;
  transform: rotate(270deg) !important;
  border-radius: 0px;
}

.vc-leaflet-control-minimap a.minimized-topright {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  border-radius: 0px;
}

.vc-leaflet-control-minimap-toggle-display {
  background-image: url('../../images/vc-overview-map-toggle.svg');
  background-size: cover;
  position: absolute;
  border-radius: 3px 0px 0px 0px;
}

.leaflet-oldie .vc-leaflet-control-minimap-toggle-display {
  background-image: url('../../images/vc-overview-map-toggle.png');
}

.vc-leaflet-control-minimap-toggle-display-bottomright {
  bottom: 0;
  right: 0;
}

.vc-leaflet-control-minimap-toggle-display-topleft {
  top: 0;
  left: 0;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.vc-leaflet-control-minimap-toggle-display-bottomleft {
  bottom: 0;
  left: 0;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.vc-leaflet-control-minimap-toggle-display-topright {
  top: 0;
  right: 0;
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

/* Old IE */
.leaflet-oldie .vc-leaflet-control-minimap {
  border: 1px solid #999;
}

.leaflet-oldie .vc-leaflet-control-minimap a {
  background-color: #fff;
}

.leaflet-oldie .vc-leaflet-control-minimap a.minimized {
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
}
