.vc-leaflet-control-minimap {
  border: solid white 4px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.65);
  border-radius: 3px;
  background: #f8f8f9;
  transition: all 0.6s;
  box-sizing: unset;
  position: absolute;
  z-index: 10; }

.vc-leaflet-control-minimap a {
  background-color: white;
  background-repeat: no-repeat;
  z-index: 99999;
  transition: all 0.6s; }

.vc-leaflet-control-minimap a.minimized-bottomright {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  border-radius: 0px; }

.vc-leaflet-control-minimap a.minimized-topleft {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  border-radius: 0px; }

.vc-leaflet-control-minimap a.minimized-bottomleft {
  -webkit-transform: rotate(270deg) !important;
  transform: rotate(270deg) !important;
  border-radius: 0px; }

.vc-leaflet-control-minimap a.minimized-topright {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  border-radius: 0px; }

